  },

  mounted() {
    console.log(this.$store.state.branchId);
    // axios.get(`/api/media`, { headers: { branchId: this.$store.state.branchId } })
    //   .then((r) => {

    //     if (!r.data.success) return console.error(r.data.msg)
    //     if(r.data.success){
    //       console.log(r.data)
    //       this.items = r.data.medias
    //     }
    //   })

    this.pageCount = Math.ceil(this.items.length / this.itemsPerPage);

    EventBus.$on("category", mediacategory => {
      this.tags = mediacategory;
      console.log(this.tags + "tags");
    });
  },

  computed: {
    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },
    videoElement() {
      return this.$refs.video;
    },
    filteredKeys() {
      return this.keys.filter(key => key !== `Name`);
    }
  },

  components: {},

  methods: {
    menubtn(item) {
      if (this.selected.length >= 2) {
        this.btndisable = true;
        console.log(this.selected.length);
      }
      if (this.selected.length < 2) {
        this.btndisable = false;
      }
    },

    mediadeleteall(index) {
      this.items.splice(index);
      //   console.log(this.number+'num')
      //   console.log(this.pageCount+'pa')

      //  console.log(this.items.length+'leng')
    },

   mediadelete(item,index) {
          


        let i=0;
        let a=0;
        let before=0;
 

 for(i=0;i<this.items.length;i++){
        for(a=0;a<this.selected.length;a++)
        {
            if(this.items[i].name === this.selected[a])
              {
                 before=this.selected.length-1;
                this.selected.splice(a, 1)
                break;
               }              
        }    
        if(this.selected.length === before)
            {
              break;
            }
        }

        for(i=0;i<this.items.length;i++)
        {
          if(this.items[i].id === item.id)
          {
          this.items.splice(i, 1)
          }
        }
    
        },


    selectdelete(item,index) {
      let i = 0;
      let a = 0;
      for (i = 0; i <= this.items.length; i++) {
        for (a = 0; a <= this.selected.length; a++) {
          if (this.items[i].name === this.selected[a]) {
            
            this.selected.splice(a,1);
            this.items.splice(i,1);
             i=0;
             a=0;
          }
        }
      
      }
    
    },

    //   console.log(this.number+'num')
    //   console.log(this.pageCount+'pa')

    //  console.log(this.items.length+'leng')

    // 선택된 체크박스 닫는 함수

    removeTask: function(index) {
      this.selected.splice(index, 1);
    },

    /// 선택된 체크박스 닫는 버튼
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
      this.pageCount = Math.ceil(this.items.length / this.itemsPerPage);
    },

    //v-chips 관련부문
    select() {
      this.selected = [];
      if (!this.selectAll) {
        for (let i in this.items) {
          this.selected.push(this.items[i].name);
        }
        return selected;
      }
    }
  }
};
</script>

